import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Button } from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';

const useStyles = makeStyles((theme) => ({
  productos: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
  },
  producto: {
    paddingTop: '30px',
    paddingBottom: '10px',
  },
  paper: {
    padding: theme.spacing(2),
    marginLeft: '6rem',
    maxWidth: 400,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregar: {
    marginRight: '1rem',
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
  botones: {
    display: 'flex',
  },

  productosCel: {
    // display: 'grid',
    // gridTemplateColumns: '1fr 1fr',
  },
  productoCel: {
    paddingTop: '0.5rem',
    paddingBottom: '0.5rem',
    marginRight: '1rem',
    marginLeft: '1rem',
  },
  paperCel: {
    padding: theme.spacing(2),
    // marginLeft: '3.5rem',
    maxWidth: 400,
  },
  imageCel: {
    width: 200,
    height: 130,
  },
  imgCel: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregarCel: {
    marginRight: '1rem',
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
  botonesCel: {
    display: 'flex',
  },
  precioCel: {
    color: '#AA3939',
  },
}))

export default function Bebidas({ addToCartBebidas }) {
    const classes = useStyles();

    const [productos] = useState ([
      {
        nombre: 'Agua',
        descripcion: 'Botella (vidrio) de agua mineral de 450ml',
        precio: 50,
        img: 'https://images.unsplash.com/photo-1546498158-7478a4cd3380?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=334&q=80',
      },
      {
        nombre: 'Coca Cola',
        descripcion: 'Botella (vidrio) de Coca-Cola de 450ml',
        precio: 70,
        img: 'https://images.unsplash.com/photo-1561758033-48d52648ae8b?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=334&q=80',
      },
      {
        nombre: 'Agua Tónica',
        descripcion: 'Lata de agua tónica de 450ml (o gaseosas Fanta o Sprite)',
        precio: 65,
        img: 'https://images.unsplash.com/photo-1581006852262-e4307cf6283a?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=334&q=80',
      },
      {
        nombre: 'Cerveza',
        descripcion: 'Lata de cerveza Old Jamaica de 450ml',
        precio: 75,
        img: 'https://images.unsplash.com/photo-1556030366-f4ec30b98e23?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=500&q=80',
      },  
    ]);

    return (
    <div>
      <Hidden only={['xs', 'sm']}>
       <div className={classes.productos}>
       {productos.map((producto, index) => (
        <div className={classes.producto} key={index}>
        <Paper className={classes.paper}>
          <Grid container spacing={2}>
            <Grid item>
              <ButtonBase className={classes.image}>
              <img 
                className= {classes.img} 
                alt= {producto.nombre} 
                src= {producto.img} 
              />
              </ButtonBase>
            </Grid>
            <Grid item xs={12} sm container>
              <Grid item xs container direction="column" spacing={2}>
                <Grid item xs>
                  <Typography gutterBottom variant="subtitle1">
                    {producto.nombre}
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    {producto.descripcion}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    ID: Bebidas
                  </Typography>
                </Grid>
                <Grid item className={classes.botones}>
                  <Typography variant="body2" style={{ cursor: 'pointer' }}>
                  <Button 
                    onClick={() => addToCartBebidas(producto)}
                    className={classes.agregar} 
                    variant="outlined"
                    >
                    Agregar
                  </Button>
                  </Typography>
                </Grid>
              </Grid>
              <Grid item>
                <Typography variant="subtitle1">${producto.precio}</Typography>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
        </div> 
      ))}
      </div>
      </Hidden>

      {/* Pantalla adaptada a Celular XS */}
      <Hidden only={['sm', 'md', 'lg', 'xl']}>
      <CssBaseline />
        <div className={classes.productosCel}>
        {productos.map((producto, index) => (
          <div className={classes.productoCel} key={index}>
          <Paper className={classes.paperCel}>
            <Grid container spacing={1}>
              <Grid item>
                <ButtonBase className={classes.imageCel}>
                <img 
                  className= {classes.imgCel} 
                  alt= {producto.nombre} 
                  src= {producto.img} 
                />
                </ButtonBase>
              </Grid>
              <Grid item xs={12} sm container>
                <Grid item xs container direction="column" spacing={2}>
                  <Grid item xs>
                    <Typography gutterBottom variant="subtitle1">
                      {producto.nombre}
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                      {producto.descripcion}
                    </Typography>
                    <Typography variant="body2" color="textSecondary">
                      ID: Bebidas
                    </Typography>
                  </Grid>
                  <Grid item className={classes.botonesCel}>
                    <Typography variant="body2" style={{ cursor: 'pointer' }}>
                    <Button 
                      onClick={() => addToCartBebidas(producto)}
                      className={classes.agregarCel} 
                      variant="outlined"
                      >
                      Agregar
                    </Button>
                    </Typography>
                  </Grid>
                </Grid>
                <Grid item>
                  <Typography variant="subtitle1" className={classes.precioCel}>${producto.precio}</Typography>
                </Grid>
              </Grid>
            </Grid>
          </Paper>
          </div> 
        ))}
        </div>
      </Hidden>
    </div>
    );
};