import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Button } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import DeleteIcon from '@material-ui/icons/Delete';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';

const useStyles = makeStyles((theme) => ({
  header: {
    display: 'flex',
    justifyContent: 'space-between',
    backgroundColor: '#444',
    height: '3rem',
    position: 'fixed',
    width: '100%',
  },
  content: {
    marginLeft: '2.7rem',
    marginRight: '3.2rem',
    alignItems: 'center',
    display: 'flex',
  },
  total: {
    alignItems: 'center',
    marginTop: '0.4rem',
    color: 'white',
  },
  icon: {
    color: '#C5C5C5',
    '&:hover': {
      color: 'white',
    }
  },
  productos: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
    paddingTop: '3rem',
  },
  producto: {
    paddingTop: '20px',
    paddingBottom: '20px',
  },
  paper: {
    padding: theme.spacing(2),
    marginLeft: '6rem',
    maxWidth: 400,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregar: {
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },

  headerCel: {
    display: 'flex',
    justifyContent: 'space-around',
    backgroundColor: '#444',
    height: '2.5rem',
  },
  contentCel: {
    // marginLeft: '2.7rem',
    // marginRight: '3.2rem',
    alignItems: 'center',
    display: 'flex',
  },
  totalCel: {
    alignItems: 'center',
    marginTop: '0.4rem',
    color: 'white',
  },
  iconCel: {
    color: '#C5C5C5',
    '&:hover': {
      color: 'white',
    }
  },
  productosCel: {
    // display: 'grid',
    // gridTemplateColumns: '1fr 1fr',
  },
  productoCel: {
    paddingTop: '0.5rem',
    paddingBottom: '0.5rem',
    marginRight: '1rem',
    marginLeft: '1rem',
  },
  paperCel: {
    padding: theme.spacing(2),
    // marginLeft: '3.5rem',
    maxWidth: 400,
  },
  imageCel: {
    width: 200,
    height: 130,
  },
  imgCel: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregarCel: {
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
  precioCel: {
    color: '#AA3939',
  },
}))

export default function Cart({ cart, clearCart, removePromoCart, removeBurgerCart, removePapasCart, removeBebidasCart }){
  const classes = useStyles(); 

  const getTotalSum = () => {
    return cart.reduce(
      (sum, { precio }) => sum + precio,
      0
    );
  };

  const productsWhatsapp = cart.map(
    (product) =>
          product.nombre + "%20" + "$" + product.precio
  );

  console.log(productsWhatsapp);

  function getLinkWhastapp() {
    var url =
      "https://api.whatsapp.com/send?phone=" +
      "541121936289" +
      "&text=" +
      encodeURIComponent("Mi pedido es: ") +
      "%0a%0a" +
      productsWhatsapp +
      "%0a" +
      encodeURIComponent("El total es: $" + getTotalSum());

    return window.open(url);
  }

  return (
    <div>
      <Hidden only={['xs', 'sm']}>
      <div className={classes.header}>
        <div className={classes.content}>
          <IconButton onClick={clearCart}>
            <DeleteIcon fontSize="large" className={classes.icon} />
          </IconButton>
          <Typography className={classes.total} variant="body1" gutterBottom>
            Eliminar Todo
          </Typography>
        </div>

        <div className={classes.content}>
          <Typography className={classes.total} variant="h6" gutterBottom>
            Total: ${getTotalSum()}
          </Typography>
        </div>

        <div className={classes.content}>
          <Typography className={classes.total} variant="body1" gutterBottom>
            Enviar mi pedido: 
          </Typography>
          <IconButton onClick={() => getLinkWhastapp()}>
            <WhatsAppIcon fontSize="large" className={classes.icon} />
          </IconButton>
        </div>
      </div>
      <div className={classes.productos}>
      {cart.map((producto, index) => (
        <div className={classes.producto} key={index}>
      <Paper className={classes.paper}>
        <Grid container spacing={2}>
          <Grid item>
            <ButtonBase className={classes.image}>
            <img 
              className= {classes.img} 
              alt= {producto.nombre} 
              src= {producto.img} 
            />
            </ButtonBase>
          </Grid>
          <Grid item xs={12} sm container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography gutterBottom variant="subtitle1">
                  {producto.nombre}
                </Typography>
                <Typography variant="body2" gutterBottom>
                  {producto.descripcion}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  ID: 1030114
                </Typography>
              </Grid>
              <Grid item>
                <Typography variant="body2" style={{ cursor: 'pointer' }}>
                <Button 
                  onClick={() => removePromoCart(producto)}
                  className={classes.agregar} 
                  variant="outlined"
                  >
                  Eliminar
                </Button>
                </Typography>
              </Grid>
            </Grid>
            <Grid item>
              <Typography variant="subtitle1">${producto.precio}</Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      </div> 
    ))}
    </div>
    </Hidden>

    {/* Pantalla adaptada a Celular XS */}
    <Hidden only={['sm', 'md', 'lg', 'xl']}>
    <CssBaseline />
    <div className={classes.headerCel}>
        <div className={classes.contentCel}>
          <IconButton onClick={clearCart}>
            <DeleteIcon className={classes.iconCel} />
          </IconButton>
          {/* <Typography className={classes.totalCel} variant="body2" gutterBottom>
            Eliminar
          </Typography> */}
        </div>

        <div className={classes.contentCel}>
          <Typography className={classes.totalCel} variant="subtitle1" gutterBottom>
            Total: ${getTotalSum()}
          </Typography>
        </div>

        <div className={classes.contentCel}>
          <Typography className={classes.totalCel} variant="body2" gutterBottom>
            Enviar: 
          </Typography>
          <IconButton onClick={() => getLinkWhastapp()}>
            <WhatsAppIcon className={classes.iconCel} />
          </IconButton>
        </div>
      </div>
      <div className={classes.productosCel}>
      {cart.map((producto, index) => (
        <div className={classes.productoCel} key={index}>
      <Paper className={classes.paperCel}>
        <Grid container spacing={2}>
          <Grid item>
            <ButtonBase className={classes.imageCel}>
            <img 
              className= {classes.imgCel} 
              alt= {producto.nombre} 
              src= {producto.img} 
            />
            </ButtonBase>
          </Grid>
          <Grid item xs={12} sm container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography gutterBottom variant="subtitle1">
                  {producto.nombre}
                </Typography>
                <Typography variant="body2" gutterBottom>
                  {producto.descripcion}
                </Typography>
                {/* <Typography variant="body2" color="textSecondary">
                  ID: 1030114
                </Typography> */}
              </Grid>
              <Grid item>
                <Typography variant="body2" style={{ cursor: 'pointer' }}>
                <Button 
                  onClick={() => removePromoCart(producto)}
                  className={classes.agregar} 
                  variant="outlined"
                  >
                  Eliminar
                </Button>
                </Typography>
              </Grid>
            </Grid>
            <Grid item>
              <Typography variant="subtitle1" className={classes.precioCel}>${producto.precio}</Typography>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      </div> 
    ))}
    </div>
    </Hidden>
  </div>
  )
}


