import React, { useState } from 'react';
import Promociones from './Promociones';
import Hamburguesas from './Hamburguesas';
import Papas from './Papas';
import Bebidas from './Bebidas';
import Cart from './Cart';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { Button, ButtonGroup } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import { withStyles } from '@material-ui/core/styles';
import Badge from '@material-ui/core/Badge';
import { grey } from '@material-ui/core/colors';
import Ayuda from './Ayuda';
import Alert from '@material-ui/lab/Alert';
import CloseIcon from '@material-ui/icons/Close';
import Collapse from '@material-ui/core/Collapse';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreVertIcon from '@material-ui/icons/MoreVert';


const StyledBadge = withStyles((theme) => ({
  badge: {
    right: -5,
    top: 13,
    border: `2px solid ${theme.palette.background.paper}`,
    padding: '0 4px',
  },
}))(Badge);

const useStyles = makeStyles((theme) => ({
  encabezado: {
    height: '9vh',
    width: '100%',
    marginTop: '3.65rem',
    backgroundColor: '#333',
    position: 'fixed',
    display: 'flex',
  },
  titulo: {
      color: 'white',
      display: 'flex',
      alignItems: 'center',
      marginLeft: '3.6rem',
  },
  botones: {
      display: 'flex',
      alignItems: 'center',
      marginLeft:'6rem',
      color: 'white',
  },
  button: {
      paddingRight: '1rem',
      paddingLeft: '1rem',
      color: 'white',
      '&:hover': {
          color: '#FFD900'
      }
  },
  cart: {
      marginLeft: '18.5rem',
      marginRight: '3.1rem',
  },
  siguiente: {
    marginTop: '0.5rem',
    marginBottom: '0.5rem',
    marginLeft: '3rem',
    borderRadius: '50px',
    backgroundColor: '#89C739',
    color: 'white',
    '&:hover': {
        backgroundColor: '#629F13',
    },
  },
  contentInfo: {
    paddingTop: '6.9rem',
    display: 'flex',
    justifyContent: 'center',
  },
  info: {
    paddingLeft: '30.3rem',
    paddingRight: '30.35rem',
  },
  section: {
      display: 'flex',
      justifyContent:'space-around',
      marginLeft: '8rem',
      marginRight: '1rem',
  },
  root: {
    flexGrow: 1,
  },
  ayuda: {
    display: 'flex',
    alignItems: 'center',
},

  encabezadoCel: {
    height: '9vh',
    width: '100%',
    marginTop: '2.78rem',
    backgroundColor: '#333',
    position: 'fixed',
    display: 'flex',
    justifyContent: 'space-around',
  },
  tituloCel: {
      color: 'white',
      display: 'flex',
      alignItems: 'center',
      // marginLeft: '3.6rem',
  },
  menuButtonCel: {
    color: 'white',
  },
  menuCel: {
    padding: 0,
  },
  buttonCel: {
    minHeight: 0,
    textTransform: 'none',
    fontSize: '12px',
      color: 'black',
      '&:hover': {
          color: '#FFD900'
      }
  },
  cartCel: {
      // marginLeft: '18.5rem',
      // marginRight: '3.1rem',
  },
  contentInfoCel: {
    paddingTop: '5.28rem',
    display: 'flex',
    justifyContent: 'center',
  },
  infoCel: {
    width: '100%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center',
    // paddingLeft: '30.3rem',
    // paddingRight: '30.35rem',
  },
  sectionCel: {
      display: 'flex',
      justifyContent:'space-around',
      marginLeft: '8rem',
      marginRight: '1rem',
  },
  rootCel: {
    flexGrow: 1,
  },
  // AyudaCel: {
  //   display: 'flex',
  //   alignItems: 'center',
  // },
}));

const PAGE_PROMOCIONES = 'promociones';
const PAGE_HAMBURGUESAS = 'hamburguesas';
const PAGE_PAPAS = 'papas';
const PAGE_BEBIDAS = 'bebidas';
const PAGE_CART = 'cart';


export default function ComplexGrid() {
  const classes = useStyles();
  const [cart, setCart] = useState ([]);
  const [page, setPage] = useState ([PAGE_PROMOCIONES, PAGE_HAMBURGUESAS, PAGE_PAPAS, PAGE_BEBIDAS]);
  const [open, setOpen] = React.useState(true);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

//---------------Funciones para eliminar del carrito--------------------------

  const removePromoCart = (eliminarPromocion) => {
    setCart(cart.filter(promocion => promocion !== eliminarPromocion))
  }

  const removeBurgerCart = (eliminarHamburguesa) => {
    setCart(cart.filter(hamburguesa => hamburguesa !== eliminarHamburguesa))
  }

  const removePapasCart = (eliminarPapa) => {
    setCart(cart.filter(papa => papa !== eliminarPapa))
  }

  const removeBebidasCart = (eliminarBebida) => {
    setCart(cart.filter(bebida => bebida !== eliminarBebida))
  }

//---------------Funciones para agregar al carrito--------------------------

  const addToCartPromo = (promocion) => {
    // console.log("Funcion de agregar al carrito");
    setCart([...cart, { ...promocion }]);
  };
  const addToCartBurger = (hamburguesa) => {
    // console.log("Funcion de agregar al carrito");
    setCart([...cart, { ...hamburguesa }]);
  };

  const addToCartPapas = (papa) => {
    // console.log("Funcion de agregar al carrito");
    setCart([...cart, { ...papa }]);
  };

  const addToCartBebidas = (bebida) => {
    // console.log("Funcion de agregar al carrito");
    setCart([...cart, { ...bebida }]);
  };

//---------------Función para pasar a otra página--------------------------

  const navegarA = (nextPage) => {
    setPage(nextPage);
  };

  const clearCart = () => {
    setCart([])
  };

  return (
    <section >
      <Hidden only={['xs', 'sm']}>
      <div className={classes.encabezado}>
            <Typography variant="subtitle1" className={classes.titulo}>
                Asistente de compra
            </Typography>
            <ButtonGroup className={classes.botones} size="small" variant="text" aria-label="text primary button group">
              
              <Button  
                onClick={() => navegarA(PAGE_PROMOCIONES)} className={classes.button}
              >
                Promociones
              </Button>
              <Button 
                onClick={() => navegarA(PAGE_HAMBURGUESAS)} className={classes.button}
              >
                Hamburguesas
              </Button>
              <Button 
                onClick={() => navegarA(PAGE_PAPAS)} className={classes.button}
              >
                Papas
              </Button>
              <Button 
                onClick={() => navegarA(PAGE_BEBIDAS)} className={classes.button}
              >
                Bebidas
              </Button>
            </ButtonGroup>
            <IconButton 
                onClick={() => navegarA(PAGE_CART)} 
                aria-label="add to shopping cart" 
                style={{ color: grey[50] }}
                className={classes.cart}
                >
                <StyledBadge badgeContent={cart.length} showZero color="secondary">
                    <ShoppingCartIcon />
                </StyledBadge>
            </IconButton>
            <div className={classes.ayuda}>
              <Ayuda />
            </div>
      </div>
      <div className={classes.contentInfo}>
        <Collapse in={open}>
          <Alert
            severity="info"
            className={classes.info}
            action={
              <IconButton
                aria-label="close"
                color="inherit"
                size="small"
                onClick={() => {
                  setOpen(false);
                }}
              >
                <CloseIcon fontSize="inherit" />
              </IconButton>
            }
          >
            Navega por el asistente de compra.
          </Alert>
        </Collapse>
      </div>
      </Hidden>

      {/* Pantalla adaptada a Celular XS */}
      <Hidden only={['sm', 'md', 'lg', 'xl']}>
      <CssBaseline />
      <div className={classes.encabezadoCel}>
            <Typography variant="body2" className={classes.tituloCel}>
                Asistente de compra
            </Typography>

            <IconButton 
                onClick={() => navegarA(PAGE_CART)} 
                aria-label="add to shopping cart" 
                style={{ color: grey[50] }}
                className={classes.cartCel}
                >
                <StyledBadge badgeContent={cart.length} showZero color="secondary">
                    <ShoppingCartIcon fontSize="small"/>
                </StyledBadge>
            </IconButton>

            <IconButton
              edge="start"
              className={classes.menuButtonCel}
              color="inherit"
              aria-label="open drawer"
            >
              <MoreVertIcon onClick={handleClick}/>
            </IconButton>
            <Menu
              className={classes.menuCel}
              id="simple-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>
                <Button  
                  onClick={() => navegarA(PAGE_PROMOCIONES)} className={classes.buttonCel}
                >
                  Promociones
                </Button>
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <Button 
                  onClick={() => navegarA(PAGE_HAMBURGUESAS)} className={classes.buttonCel}
                >
                  Hamburguesas
                </Button>
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <Button 
                  onClick={() => navegarA(PAGE_PAPAS)} className={classes.buttonCel}
                >
                  Papas
                </Button>
              </MenuItem>
              <MenuItem onClick={handleClose}>
                <Button 
                  onClick={() => navegarA(PAGE_BEBIDAS)} className={classes.buttonCel}
                >
                  Bebidas
                </Button>
              </MenuItem>
            </Menu>

            {/* <div className={classes.AyudaCel}>
              <Ayuda />
            </div> */}
      </div>
      <div className={classes.contentInfoCel}>
        <Collapse in={open}>
          <Alert
            severity="info"
            className={classes.infoCel}
            action={
              <IconButton
                aria-label="close"
                color="inherit"
                size="small"
                onClick={() => {
                  setOpen(false);
                }}
              >
                <CloseIcon fontSize="inherit" />
              </IconButton>
            }
          >
            Navega por el asistente de compra.
          </Alert>
        </Collapse>
      </div>
      </Hidden>

        {page === PAGE_PROMOCIONES && (
            <Promociones addToCartPromo={addToCartPromo} />
        )}
        {page === PAGE_HAMBURGUESAS && (
            <Hamburguesas addToCartBurger={addToCartBurger} />
        )}
        {page === PAGE_PAPAS && (
            <Papas addToCartPapas={addToCartPapas} />
        )}
        {page === PAGE_BEBIDAS && (
            <Bebidas addToCartBebidas={addToCartBebidas} />
        )}
        {page === PAGE_CART && (
          <Cart 
            cart={cart} 
            removePromoCart={removePromoCart}
            removeBurgerCart={removeBurgerCart}
            removePapasCart={removePapasCart}
            removeBebidasCart={removeBebidasCart}
            clearCart={clearCart}
          />
        )}
    </section>
  );
}
