import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ButtonBase from '@material-ui/core/ButtonBase';
import { Button } from '@material-ui/core';
import Hidden from '@material-ui/core/Hidden';
import CssBaseline from '@material-ui/core/CssBaseline';

const useStyles = makeStyles((theme) => ({
  productos: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
  },
  producto: {
    paddingTop: '30px',
    paddingBottom: '10px',
  },
  paper: {
    padding: theme.spacing(2),
    marginLeft: '6rem',
    maxWidth: 400,
  },
  image: {
    width: 128,
    height: 128,
  },
  img: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregar: {
    marginRight: '1rem',
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
  botones: {
    display: 'flex',
  },

  productosCel: {
    // display: 'grid',
    // gridTemplateColumns: '2fr 2fr',
  },
  productoCel: {
    paddingTop: '0.5rem',
    paddingBottom: '0.5rem',
    marginRight: '1rem',
    marginLeft: '1rem',
  },
  paperCel: {
    padding: theme.spacing(2),
    // marginLeft: '3.5rem',
    maxWidth: 400,
  },
  imageCel: {
    width: 128,
    height: 128,
  },
  imgCel: {
    margin: 'auto',
    display: 'block',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  agregarCel: {
    marginRight: '1rem',
    backgroundColor: '#FFD900',
    '&:hover': {
      backgroundColor: '#D8B800',
    }
  },
  botonesCel: {
    display: 'flex',
  },
  precioCel: {
    color: '#AA3939',
  },
}))

export default function Promociones({ addToCartPromo }) {
    const classes = useStyles();

    const [productos] = useState ([
        {
          nombre: 'Promo 1',
          descripcion: 'Hamburguesa Súper + Papas + Gaseosa',
          precio: 500,
          img: 'https://images.unsplash.com/photo-1607013401178-f9c15ab575bb?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=750&q=80',
        },
        {
          nombre: 'Promo 2',
          descripcion: 'Hamburguesa con lechuga y tomate + Fritas',
          precio: 380,
          img: 'https://images.unsplash.com/photo-1551782450-a2132b4ba21d?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=750&q=80',
        },
        {
          nombre: 'Promo 3',
          descripcion: '3 Hamburguesas Completas (ingredientes a elección)',
          precio: 700,
          img: 'https://images.unsplash.com/photo-1585730315692-5252e57d4b40?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=626&q=80',
        },
        {
          nombre: 'Promo 4',
          descripcion: 'Hamburguesa con lechuga, tomate y cheddar + Bebida',
          precio: 320,
          img: 'https://images.unsplash.com/photo-1551615593-ef5fe247e8f7?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=780&q=80',
        },
        
      ]);



    return (
    <div>
      <Hidden only={['xs', 'sm']}>
       <div className={classes.productos}>
       {productos.map((producto, index) => (
        <div className={classes.producto} key={index}>
        <Paper className={classes.paper}>
          <Grid container spacing={2}>
            <Grid item>
              <ButtonBase className={classes.image}>
              <img 
                className= {classes.img} 
                alt= {producto.nombre} 
                src= {producto.img} 
              />
              </ButtonBase>
            </Grid>
            <Grid item xs={12} sm container>
              <Grid item xs container direction="column" spacing={2}>
                <Grid item xs>
                  <Typography gutterBottom variant="subtitle1">
                    {producto.nombre}
                  </Typography>
                  <Typography variant="body2" gutterBottom>
                    {producto.descripcion}
                  </Typography>
                  <Typography variant="body2" color="textSecondary">
                    ID: Promociones
                  </Typography>
                </Grid>
                <Grid item className={classes.botones}>
                  <Typography variant="body2" style={{ cursor: 'pointer' }}>
                  <Button 
                    onClick={() => addToCartPromo(producto)}
                    className={classes.agregar} 
                    variant="outlined"
                    >
                    Agregar
                  </Button>
                  </Typography>

                </Grid>
              </Grid>
              <Grid item>
                <Typography variant="subtitle1">${producto.precio}</Typography>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
        </div> 
      ))}
      </div>
      </Hidden>

      {/* Pantalla adaptada a Celular XS */}
      <Hidden only={['sm', 'md', 'lg', 'xl']}>
      <CssBaseline />
        <div className={classes.productosCel}>
        {productos.map((producto, index) => (
          <div className={classes.productoCel} key={index}>
          <Paper className={classes.paperCel}>
            <Grid container spacing={1}>
              <Grid item className={classes.contentCel}>
                <ButtonBase className={classes.imageCel}>
                <img 
                  className= {classes.imgCel} 
                  alt= {producto.nombre} 
                  src= {producto.img} 
                />
                </ButtonBase>
              </Grid>
              <Grid item xs={12} sm container>
                <Grid item xs container direction="column" spacing={2}>
                  <Grid item xs>
                    <Typography gutterBottom variant="subtitle1">
                      {producto.nombre}
                    </Typography>
                    <Typography variant="body2" gutterBottom>
                      {producto.descripcion}
                    </Typography>
                    {/* <Typography variant="body2" color="textSecondary">
                      ID: Promociones
                    </Typography> */}
                  </Grid>
                  <Grid item className={classes.botonesCel}>
                    <Typography variant="body2" style={{ cursor: 'pointer' }}>
                    <Button 
                      onClick={() => addToCartPromo(producto)}
                      className={classes.agregarCel} 
                      variant="outlined"
                      size="small"
                      >
                      Agregar
                    </Button>
                    </Typography>

                  </Grid>
                </Grid>
                <Grid item>
                  <Typography variant="subtitle1" className={classes.precioCel}>${producto.precio}</Typography>
                </Grid>
              </Grid>
            </Grid>
          </Paper>
          </div> 
        ))}
        </div>
      </Hidden>
    </div>
    );
};